import React, { useEffect, useRef, useState } from "react";
import Image from "./Image";
import reactImageSize from 'react-image-size';
import { DetectPhone, convertObjectToArray } from '../../../../utils'
import Video from "./Video";

const Media = (props) => {

  const [height, setHeight] = useState('100')
  const [width, setWidth] = useState('100')
  const [playing, setPlaying] = useState(false)              // Video playing State
  const [error, setError] = useState('')
  const [videoLoaded, setVideoLoaded] = useState(false)    // Video loaded state
  const [isMobile, setIsMobile] = useState(false)
  const ref = useRef();
  const videoRef = useRef() 

  useEffect(() => {
    const { item } = props;

    setIsMobile(DetectPhone())

    if (item.mediaHeight && item.mediaWidth != "") {
      setHeight((item.mediaHeight * 100) / item.mediaWidth)
      setWidth((item.mediaWidth * 100) / item.mediaHeight)

    }
    else {
      reactImageSize(item.postFileNew).then(({ width, height }) => {
        setHeight((height * 100) / 100)
        setWidth((width * 100) / 100)
      }).catch((errorMessage) => {
        setHeight((height * 100) / 100)
        setWidth((width * 100) / 100)
        setError(errorMessage)
      })

    }
  }, [])

  useEffect(() => {
    // play video as is appears on viewport
    setPlaying(props.isVisible)
  }, [props.isVisible])


  // play video from starting after 4s
  const onPlayingVideo = (e) => {
    if (e.playedSeconds >= 3) {
      videoRef.current.seekTo(0)
    }
  }

  const { item, wall, handleAllImageError, isVisible, itemIndex, addLoadedMedia, loadedMedia } = props;
  const ugc_products = convertObjectToArray(item.ugc_products);


  return (
    <div className="ts_rt_post_image" ref={ref}>
      <div className="ts_rt_media_wrap">
        {(item.type == 3 || item.type == 5) ?
          <Video videoRef={videoRef} inViewport={isVisible} item={item} loadedMedia={loadedMedia} isMobile={isMobile} addLoadedMedia={addLoadedMedia} wall={wall} itemIndex={itemIndex} setVideoLoaded={setVideoLoaded} playing={playing} onPlayingVideo={onPlayingVideo} videoLoaded={videoLoaded} handleAllImageError={handleAllImageError} />
          : width ? <Image handleAllImageError={handleAllImageError} inViewport={isVisible} ImageClass={'ts_rt_image'} itemIndex={itemIndex} height={height} width={width} ImageUrl={item.postFileNew} ugc_products={ugc_products} item={item} wall={wall} /> : ''}
      </div>
    </div>
  );
}

export default Media