import React from 'react'
import ReactPlayer from "react-player"

import LazyLoad from 'react-lazyload';

const Video = (props) => {
  const { videoRef, inViewport, item, wall, setVideoLoaded, onPlayingVideo, videoLoaded, handleAllImageError, addLoadedMedia, loadedMedia, isMobile } = props
  return (
    <>
      {!isMobile ?
        <LazyLoad unmountIfInvisible={true} style={videoLoaded ? {height:'100%'} : {height:'0px'}} debounce={true} >
          <ReactPlayer
            ref={videoRef}
            poster={item.postFileNew}
            className="tb-detail-image-iframe"
            url={inViewport || loadedMedia.includes(item.id) ? item.mediaUrl : ''}
            // url={inViewport ? item.mediaUrl : ''}
            data-type="video"
            data-network={item.networkId}
            data-link={item.link}
            data-wall-id={wall.Wall.id} data-item-id={item.id}
            data-load={0}
            config={{
              facebook: {
                appId: '117029975354072',
                attributes: {
                  'data-width': 'auto',
                  // 'data-height': `${document.querySelector('.ts_rt_post_in') ? `${document.querySelector('.ts_rt_post_in').clientHeight} px` : '100%'}`
                }
              }
            }}
            loop={true}
            onError={(e) => { handleAllImageError(e); setVideoLoaded(false) }}
            autoPlay={inViewport}
            muted
            playsinline={true}
            onReady={(e) => { setVideoLoaded(true); addLoadedMedia(item.id) }}
            playing={inViewport}
            height={'100%'}
            width={'100%'}
            onProgress={onPlayingVideo}
          style={videoLoaded ? { position: 'relative', opacity: 1 } : { opacity:1 }}
          /> </LazyLoad>  :
          <LazyLoad style={{height:'100%'}}>
        <div className={'ts_rt_image reelThemeCard sk_img'} >
          <svg xmlns="http://www.w3.org/2000/svg" version="1.1" width="512" height="512" x="0" y="0" viewBox="0 0 439.575 439.575" className=""><g><path d="M375.741 63.834c-86.554-85.707-226.199-85.021-311.906 1.533-85.112 85.953-85.112 224.421 0 310.374 86.554 85.707 226.199 85.021 311.906-1.533 85.112-85.953 85.112-224.421 0-310.374zM219.788 422.618c-111.842 0-202.831-90.988-202.831-202.831s90.988-202.83 202.831-202.83 202.831 90.988 202.831 202.831-90.989 202.83-202.831 202.83z" fill="#FFFFFF" opacity="1" data-original="#FFFFFF" className=""></path><path d="M339.349 212.108v.01L160.277 108.733a8.861 8.861 0 0 0-13.292 7.671v206.769a8.861 8.861 0 0 0 13.293 7.67L339.35 227.458a8.862 8.862 0 0 0-.001-15.35z" fill="#FFFFFF" opacity="1" data-original="#FFFFFF" className=""></path></g></svg>
          <img loading={inViewport ? "eager" : "lazy"} fetchpriority={inViewport ? "high" : "auto"} data-src={item.postFileNew} src={item.postFileNew} data-is-optmized="0" data-link={item.link} data-load="0" data-wall-id={wall.Wall.id} data-item-id={item.id} data-filter-id={item.filterId} data-stories={item.stories} data-network={item.networkId} width="auto" height="100%" className={`imgID${item.id}`} onError={(e) => { if (item.networkId !== `28`) handleAllImageError(e) }} alt="post" />
        </div>
        </LazyLoad>
         }

      {videoLoaded ?
        <></>
        :
        <div className={'ts_rt_image reelThemeCard sk_img'} >
          <img loading={inViewport ? "eager" : "lazy"} fetchpriority={inViewport ? "high" : "auto"} data-src={item.postFileNew} src={item.postFileNew} data-is-optmized="0" data-link={item.link} data-load="0" data-wall-id={wall.Wall.id} data-item-id={item.id} data-filter-id={item.filterId} data-stories={item.stories} data-network={item.networkId} width="auto" height="100%" className={`imgID${item.id}`} onError={(e) => { if (item.networkId !== `28`) handleAllImageError(e) }} alt="post" />
        </div>
      }

    </>
  )
}

export default (Video)